.about-company__content {
  @include desktop-content;
  display: flex;
  flex-direction: column;
  position: relative;
  max-width: 1200px;
  padding-top: 76px;
  padding-left: 204px;

  @media (max-width: 1023px) {
    @include tablet-content;
    padding-top: 23px;
    padding-left: 210px;
  }

  @media (max-width: 767px) {
    @include mobile-content;
    padding-top: 36px;
  }
}

.about-company__heading {
  @include font-roboto-condensed;
  margin-top: 0;
  margin-bottom: 20px;
  max-width: 300px;
  font-size: 36px;
  font-weight: normal;
  line-height: 51px;
  color: #666666;

  @media (max-width: 1023px) {
    margin-bottom: 24px;
    max-width: 200px;
    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: 767px) {
    margin-bottom: 21px;
    font-size: 28px;
    line-height: 39px;
  }
}

.about-company__description {
  @include font-pt-mono;
  margi-topn: 0;
  margin-bottom: 20px;
  max-width: 980px;
  font-size: 16px;
  line-height: 22px;
  color: #666666;

  &:last-of-type {
    margin-bottom: 40px;
  }

  @media (max-width: 1023px) {
    padding-left: 4px;
    max-width: 650px;
  }

  @media (max-width: 767px) {
    margin-bottom: 40px;
    padding-left: 3px;
    max-width: 500px;
    min-width: 290px;

    &:last-of-type {
      margin-bottom: 30px;
    }
  }
}

.about-company__points {
  display: none;

  @media (max-width: 1023px) {
    display: inline;
  }
}

.about-company__longtext {
  @media (max-width: 1023px) {
    display: none;
  }
}

.about-company__link {
  @include font-roboto-condensed;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 110px;
  width: 180px;
  height: 45px;
  border: 1px solid $lightblue;
  border-radius: 5px;

  color: $lightblue;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $lightblue;
    color: $white;
  }

  &:active {
    opacity: 0.7;
  }

  @media (max-width: 1023px) {
    margin-bottom: 84px;
    margin-left: 3px;
  }

  @media (max-width: 767px) {
    margin-bottom: 64px;
  }
}

.about-company__year {
  position: absolute;
  top: 81px;
  left: -6px;

  @media (max-width: 1023px) {
    top: 20px;
    left: 0;
  }

  @media (max-width: 767px) {
    display: none;
  }
}
